import React from 'react'
import { useState } from 'react';


export const TestVideo = () => {
  const [isPlay, setIsPlay] = useState(false);
  const video = document.querySelector('.video1');

  const startPlayback = () => {
    let video = document.querySelector('.video1');
    if (isPlay) {
      setIsPlay(false);
      video.pause();
    } else {
      setIsPlay(true)
      video.play();
    }
  }

  return (
    <div onClick={() => startPlayback()}>Start Playback
      <video src="https://www.learningcontainer.com/wp-content/uploads/2020/05/sample-mp4-file.mp4" width="100%" height="auto" playsInline webkit-playsinline className="video1" preload="auto">
      </video>
    </div> 
  )
};